.wrapper {
  height: 100%;
  display: grid;
  justify-content: space-around;
  align-items: flex-start;
  overflow: auto;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
}

@media (max-width: 700px) {
  .wrapper {
    grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
  }
}